import { DatePipe } from '@angular/common';
import { Component, ElementRef, HostListener, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, ParamMap, Router } from '@angular/router';
import { ChartDataSets, ChartOptions, ChartType } from 'chart.js';
import { PiService } from '../../service/pi.service';
import { NotificationsService } from 'angular2-notifications';
import { error } from '@angular/compiler/src/util';
import { Chart } from 'chart.js';
import { BaseChartDirective, Color, Label } from 'ng2-charts';
import { ChartColors, central, southern, eastern, northeastern, northern, western } from 'src/app/constant/ChartColors';

declare const bootstrap: any;

@Component({
    selector: 'app-evaluate',
    templateUrl: './summary.component.html',
    styleUrls: ['./summary.component.css']
})
export class SummaryComponent implements OnInit {
    filtersLoaded: Promise<boolean>;

    region: string;
    province: string;
    site: string;
    pi: string;
    lat = '13.8591';
    lng = '100.5217';
    mobile: boolean;
    moblieChart: boolean;

    isNMModule: boolean = false;
    isRectifier: boolean = false;
    isStatus: boolean = false;
    isEvaluate: boolean = false;
    isEnergySaving: boolean = false;
    buttonRight: any;
    buttonLeft: any;
    gotoMapModal: any;
    @ViewChild('sohChart', { static: true }) soh: ElementRef;
    @ViewChild('battChart', { static: true }) battQuantity: ElementRef;
    @ViewChild('timeChart', { static: true }) timeBackup: ElementRef;

    selectData = 2;
    time = '';
    piId: any;
    piInfo: any;
    evaluateInfo: any;
    battInfo: any;

    regionBackup: string[] = ['central', 'northern', 'southern', 'eastern', 'northeastern', 'western'];

    constructor(private ar: ActivatedRoute, private piService: PiService,
        private notiService: NotificationsService,
        private router: Router,
        private activeRoute: ActivatedRoute) { }

    ngOnInit(): void {
        this.activeRoute.paramMap.subscribe((params: ParamMap) => {
            this.piId = params.get('piId');
        });

        this.gotoMapModal = new bootstrap.Modal(document.getElementById('gotoMapModal'));

        this.initialiseInvites();
        this.getPiInfo();
        this.searchData();
        this.isNMModule = true;

        this.buttonRight = document.getElementById('slideRight');
        this.buttonLeft = document.getElementById('slideLeft');

        if (window.innerWidth <= 1200) {
            this.mobile = true;
        } else {
            this.mobile = false;
        }

        if (window.innerWidth <= 992) {
            this.moblieChart = true;
        } else {
            this.moblieChart = false;
        }
    }

    back() {
        let path = '/region/' + this.region.toLocaleLowerCase() + '/' + this.province.toLocaleLowerCase() + '/' + this.site.toLocaleLowerCase();
        this.router.navigate([path]);
    }

    @HostListener('window:resize', ['$event'])
    onResize(event) {
        if (window.innerWidth <= 1200) { // 1280px portrait
            this.mobile = true;
        } else {
            this.mobile = false;
        }

        if (window.innerWidth <= 992) {
            this.moblieChart = true;
        } else {
            this.moblieChart = false;
        }
    }

    getPiInfo() {
        this.piService.getPiStatus(this.piId).subscribe(
            data => {
                this.piInfo = data;
                // this.setMap();
                this.getBattInfo(this.piInfo.battery[0].batteryId, 'NM Module');
            },(err) => {
                if (err.status != 401) {
                    this.notiService.error("", "");
                }
            });
    }

    getBattInfo(batteryId: any,mode: string) {
        this.piService.getPiStatusBatt(batteryId).subscribe(
            data => {
                this.battInfo = data;
                this.setShowMode(mode);
            },
            (err) => {
                if (err.status != 401) {
                    this.notiService.error("", "");
                }
            });
    }

    searchData() {
        this.changeTimeLabel();
        this.createLabel(this.daysInThisMonth());
        this.piService.getPiEvaluate(this.piId, this.selectData).subscribe(
            data => {
                this.evaluateInfo = data;
            },
            (err) => {
                if (err.status != 401) {
                    this.notiService.error("", "");
                }
            });
    }

    initialiseInvites() {
        if (this.ar.snapshot.params.region) {
            this.region = this.ar.snapshot.params.region;
        }
        if (this.ar.snapshot.params.province) {
            this.province = this.ar.snapshot.params.province;
        }
        if (this.ar.snapshot.params.site) {
            this.site = this.ar.snapshot.params.site;
        }
        if (this.ar.snapshot.params.pi) {
            this.pi = this.ar.snapshot.params.pi;
        }
    }

    selectDataFnc(select) {
        this.selectData = select;
        this.searchData();
    }

    onClickNext() {
        document.getElementById('container').scrollLeft += 100;
    };
    onClickBack() {
        document.getElementById('container').scrollLeft -= 100;
    };

    changeTimeLabel() {
        let datepipe: DatePipe = new DatePipe('en-US')
        //time
        if (this.selectData === 1) {
            let today = new Date();
            this.time = '1-' + datepipe.transform(today, 'dd MMMM yyyy');
        } else if (this.selectData === 2) {
            let today = new Date();
            this.time = datepipe.transform(today, 'MMMM yyyy');
        } else if (this.selectData === 3) {
            let today = new Date();
            let month = today.getMonth() == 0 ? 11 : today.getMonth() - 1;
            let year = today.getMonth() == 0 ? today.getFullYear() - 1 : today.getFullYear();
            today.setMonth(month);
            today.setFullYear(year);
            this.time = datepipe.transform(today, 'MMMM yyyy');
        }
    }

    // events
    public chartClicked({ event, active }: { event: MouseEvent, active: {}[] }): void {
        // console.log(event, active);
    }

    public chartHovered({ event, active }: { event: MouseEvent, active: {}[] }): void {
        // console.log(event, active);
    }

    addData() {
        this.barChartDatas = [];
        this.piInfo.batterySohAvg.forEach(e => {
            this.barChartDatas.push(e.avhSoh);
        });
        this.barChartData = [
            {
                data: this.barChartDatas
                , label: 'SOH'
                , type: 'line'
                , borderWidth: 2
            }
        ];
        this.barChartDataTarget = [];
        this.barChartDataActual = [];
        this.piInfo.batteryQuantity.target.forEach(e => {
            this.barChartDataTarget.push(e.unit);
        });
        this.piInfo.batteryQuantity.actual.forEach(e => {
            this.barChartDataActual.push(e.unit);
        });
        this.barChartDataBatt = [
            {
                data: this.barChartDataTarget
                , label: 'Target'
                , type: 'line'
                , borderWidth: 3
            },
            {
                data: this.barChartDataActual
                , label: 'Actual'
                , type: 'line'
                , borderWidth: 3
            }
        ];
        this.barChartDatasTime = [];
        this.piInfo.totalBackupTime.forEach(e => {
            this.barChartDatasTime.push(e.min);
        });
        this.barChartDataTime = [
            {
                data: this.barChartDatasTime
                , label: 'Backup time'
            }
        ];
    }

    // Chart-1
    public barChartOptions: ChartOptions = {
        responsive: true,
        elements: {
            line: {
                fill: false
            }
        },
        animation: {
            animateScale: true,
            animateRotate: true
        },
        legend: {
            display: true,
            position: 'bottom',
            labels: {
                boxWidth: 8,
                usePointStyle: true
            }
        },
        scales: {
            xAxes: [{
                stacked: false,
                gridLines: {
                    display: false,
                },
                ticks: {
                    max: 31,
                    min: 1,
                    stepSize: 5,
                    fontSize: 9,
                    maxRotation: 0,
                    minRotation: 0,
                    autoSkip: false,
                    fontFamily: 'Prompt-Light',
                    fontColor: '#A4A4A4'
                }
            }],
            yAxes: [{
                ticks: {
                    min: 0,
                    fontSize: 9,
                    fontFamily: 'Prompt-Light',
                    fontColor: '#A4A4A4'
                },
                stacked: false,
                gridLines: {
                    display: true,
                    color: '#E7E7E7'
                },
                scaleLabel: {
                    display: true,
                    labelString: 'Percent',
                    fontFamily: 'Prompt-Light',
                    lineHeight: 1,
                    fontSize: 10
                }
            }]
        }
    };
    public barChartType: ChartType = 'line';
    public barChartLegend = true;
    public barChartLabels: string[];
    public barChartDatas = [];
    public barChartData: ChartDataSets[] = [];
    public chartColors: Array<any> = [];

    // Chart-2
    public barChartOptionsBatt: ChartOptions = {
        responsive: true,
        elements: {
            line: {
                fill: false
            },
            point: {
                radius: 0
            }
        },
        animation: {
            animateScale: true,
            animateRotate: true
        },
        legend: {
            display: true,
            position: 'bottom',
            labels: {
                boxWidth: 8,
                usePointStyle: true
            }
        },
        scales: {
            xAxes: [{
                stacked: false,
                gridLines: {
                    display: true,
                },
                ticks: {
                    max: 31,
                    min: 1,
                    stepSize: 5,
                    fontSize: 9,
                    maxRotation: 0,
                    minRotation: 0,
                    autoSkip: false,
                    maxTicksLimit: 6,
                    fontFamily: 'Prompt-Light',
                    fontColor: '#A4A4A4'
                }
            }],
            yAxes: [{
                ticks: {
                    max: 2,
                    min: 0,
                    stepSize: 1,
                    fontSize: 9,
                    fontFamily: 'Prompt-Light',
                    fontColor: '#A4A4A4'
                },
                stacked: false,
                gridLines: {
                    display: true,
                    color: '#E7E7E7'
                },
                scaleLabel: {
                    display: true,
                    labelString: 'Unit',
                    fontFamily: 'Prompt-Light',
                    lineHeight: 1,
                    fontSize: 10
                }
            }]
        }
    };
    public barChartTypeBatt: ChartType = 'line';
    public barChartLegendBatt = true;

    public barChartLabelsBatt: string[];
    public barChartDataTarget = [];
    public barChartDataActual = [];

    public barChartDataBatt: ChartDataSets[] = [];
    public chartColorsBatt: Array<any> = [
        { // first color
            backgroundColor: '#018ABE',
            borderColor: '#018ABE',
            lineTension: 0,
            pointBackgroundColor: '#018ABE',
            pointBorderColor: '#fff',
            pointHoverBackgroundColor: '#018ABE',
            pointHoverBorderColor: 'rgba(225,10,24,0.2)',
            spanGaps: true
        },
        { // second color
            backgroundColor: '#65D3BA',
            borderColor: '#65D3BA',
            lineTension: 0,
            pointBackgroundColor: '#65D3BA',
            pointBorderColor: '#fff',
            pointHoverBackgroundColor: '#65D3BA',
            pointHoverBorderColor: 'rgba(225,10,24,0.2)',
            spanGaps: true
        }
    ];

    // Chart-3
    public barChartOptionsTime: ChartOptions = {
        responsive: true,
        elements: {
            line: {
                fill: false
            }
        },
        animation: {
            animateScale: true,
            animateRotate: true
        },
        legend: {
            display: true,
            position: 'bottom',
            labels: {
                boxWidth: 8,
                usePointStyle: true
            }
        },
        scales: {
            xAxes: [{
                stacked: false,
                gridLines: {
                    display: false,
                },
                ticks: {
                    max: 31,
                    min: 1,
                    stepSize: 5,
                    fontSize: 9,
                    maxRotation: 0,
                    minRotation: 0,
                    autoSkip: false,
                    fontFamily: 'Prompt-Light',
                    fontColor: '#A4A4A4'
                }
            }],
            yAxes: [{
                ticks: {
                    min: 0,
                    stepSize: 100,
                    fontSize: 9,
                    fontFamily: 'Prompt-Light',
                    fontColor: '#A4A4A4'
                },
                stacked: false,
                gridLines: {
                    display: true,
                    color: '#E7E7E7'
                },
                scaleLabel: {
                    display: true,
                    labelString: 'Mins',
                    fontFamily: 'Prompt-Light',
                    lineHeight: 1,
                    fontSize: 10
                }
            }]
        }
    };
    public barChartTypeTime: ChartType = 'bar';
    public barChartLegendTime = true;

    public barChartLabelsTime: string[];
    public barChartDatasTime = [];

    public barChartDataTime: ChartDataSets[] = [];
    public chartColorsTime: Array<any> = [];

    // function

    createLabel(dayInMonth) {
        this.barChartLabels = [];
        this.barChartLabelsBatt = [];
        this.barChartLabelsTime = [];
        for (let index = 0; index < dayInMonth; index++) {
            let label = index + 1;
            if (label % 5 == 0) {
                this.barChartLabels.push(label.toString());
                this.barChartLabelsBatt.push(label.toString());
                this.barChartLabelsTime.push(label.toString());
            } else {
                this.barChartLabels.push('');
                this.barChartLabelsBatt.push('');
                this.barChartLabelsTime.push('');
            }
        }
    }

    daysInThisMonth() {
        let now = new Date();
        let dayInMonth = 0;
        if (this.selectData === 1) {
            dayInMonth = new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate();
        } else if (this.selectData === 2) {
            dayInMonth = new Date(now.getFullYear(), now.getMonth() + 1, 0).getDate();
        } else if (this.selectData === 3) {
            let month = now.getMonth() == 1 ? 12 : now.getMonth();
            let year = now.getMonth() == 1 ? (now.getFullYear() - 1) : now.getFullYear();
            now.setMonth(month);
            now.setFullYear(year);
            dayInMonth = new Date(now.getFullYear(), now.getMonth(), 0).getDate();
        }
        return dayInMonth;
    }

    setShowMode(mode: string) {
        this.isNMModule = false;
        this.isRectifier = false;
        this.isStatus = false;
        this.isEvaluate = false;
        this.isEnergySaving = false;

        if (mode == "NM Module") {
            this.isNMModule = true;
        } else if (mode == "Rectifier") {
            this.isRectifier = true;
        } else if (mode == "Status") {
            this.isStatus = true;
        } else if (mode == "Evaluate") {
            this.isEvaluate = true;
        } else if (mode == "Energy Saving") {
            this.isEnergySaving = true;
        }
    }

    gotoGoogleMaps() {
        this.gotoMapModal.show();
    }
}
