import { AccountLevelService } from './account-level.service';
import { Component, OnInit } from '@angular/core';

declare const bootstrap: any;

@Component({
  selector: 'app-account-level',
  templateUrl: './account-level.component.html',
  styleUrls: ['./account-level.component.css']
})
export class AccountLevelComponent implements OnInit {

  companyList: any = [];
  companyForm: any = {
    companyName: ''
  }
  isVerify: any = false;
  companySelected: any = null;
  companyFromModal:any;
  messageModal: any;
  messageAlert: any = {
    type: 'error',
    title: '',
    detail: '',
    buttonOption: [
      {
        label: 'Cancel',
        class: '',
        action: this.hideMessageModal.bind(this)
      },
      {
        label: 'Retry',
        class: 'btn-outline-blue',
        action: function () {
        }.bind(this)
      },
    ]
  }
  constructor(private service: AccountLevelService) { }

  ngOnInit() {
    this.getCompanyList();
  }

  async ngAfterViewInit() {
    this.companyFromModal = new bootstrap.Modal(document.getElementById('companyFromModal'));
    this.messageModal = new bootstrap.Modal(document.getElementById('messageModal'));
  }

  async getCompanyList() {
    const data: any = await this.service.getCompanyList();
    this.companyList = data;
    this.isVerify = false;
    this.companyForm = {
      companyName: ''
    }
  }

  verify() {
    this.isVerify = true;
    if (!this.companyForm.companyName) {
      return false;
    }
    return true;
  }

  async openEditModuleModal(company: any) {
    this.companyForm = company;
    this.companyFromModal.show();
  }

  openMessageSaveModal() {
    this.messageAlert = {
      type: 'error',
      title: 'Do you want to save?',
      detail: 'Do you sure want to save changes?',
      buttonOption: [
        {
          label: 'Cancel',
          class: '',
          action: this.hideMessageModal.bind(this)
        },
        {
          label: 'Save',
          class: 'btn-outline-blue',
          action: async function () {
            this.hideMessageModal();
            if (this.verify()) {
              if (!this.companyForm.companyId) {
                await this.service.insertCompany(this.companyForm);
              } else if (this.companyForm.companyId) {
                await this.service.updateCompany(this.companyForm);
                this.companyFromModal.hide();
              }
              await this.getCompanyList();
            }
          }.bind(this)
        },
      ]
    }
    this.messageModal.show();
  }

  openMessageDeleteModal(company: any) {
    this.companySelected = company;
    this.messageAlert = {
      type: 'error',
      title: 'Are you sure?',
      detail: 'Are you sure want to delete?',
      buttonOption: [
        {
          label: 'Cancel',
          class: '',
          action: this.hideMessageModal.bind(this)
        },
        {
          label: 'Delete',
          class: 'btn-outline-rad',
          action: async function () {
            await this.service.deleteCompany(this.companySelected.companyId);
            this.hideMessageModal();
            await this.getCompanyList();
          }.bind(this)
        },
      ]
    }
    this.messageModal.show();
  }

  hideMessageModal() {
    this.messageModal.hide();
  }

}
